import { isTagEmbed, isTagbox } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Media = ({ itemData, wallID, ownerId, wall }) => {
  const autoPlayTagEmbed = isTagEmbed && (itemData.type === 3 || itemData.type === 5) && !wall.Personalization.mobilePopup && !wall.Personalization.postFeatured ? true : false
  const autoPlay = (itemData.type === 3 || itemData.type === 5) && itemData.network.id !== 28 && wall.ThemeRule.autoPlay == 1 ? true : false
  const size = isTagEmbed || isTagbox ? wall.ThemeRule.aspectImageRatio ? wall.ThemeRule.aspectImageRatio : false : false


  return <div className="tb_wt_media_wrap" role="button" tabindex="0" aria-label="Post Pop up media wrapper">
    {!autoPlayTagEmbed ? <MediaIcon itemData={itemData} IconClass="tb_wt_media_icon" show={true} isCenter={isTagEmbed} /> : null}
    {autoPlay || autoPlayTagEmbed ?
      <Video VideoClass="tb_wt_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} isCover={true} controls={false} autoPlay={false} muted={true} />
      :
      <Image ImageClass="tb_wt_image" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={size} ThemeRule={wall.ThemeRule} />
    }
  </div>
}
export default Media